import { Icon, Input } from 'antd';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import ProxyChecker from '../components/proxy-checker';
import {
  CheckProxyButtonDiv,
  ElementContainer,
  LoaderDiv,
  SaveAndNextBtn,
  Subtitle,
} from '../domElements';
import { IProxyChecked } from '../interfaces';
import { checkProxy, PROXY_CHECKED_INIT } from '../utils/check-proxy';

declare interface IHttpSocksProxyForm {
  profileInfo: any;
  setUpdatedField: (value: string) => void;
  updateProfileInfo: (params: any) => void;
  isCheckInterrupted: boolean;
  setIsCheckInterrupted: React.Dispatch<React.SetStateAction<boolean>>;
}

let ipcRenderer: Electron.IpcRenderer;
const isElectron = !!window.require;
if (isElectron) {
  ipcRenderer = window.require('electron').ipcRenderer;
}

const HttpSocksProxyForm: React.FC<IHttpSocksProxyForm> = (props) => {
  const [proxyChecked, setProxyChecked] = useState<IProxyChecked>(PROXY_CHECKED_INIT);
  const [isLoading, changeLoading] = useState<boolean>(false);

  const { profileInfo, updateProfileInfo, isCheckInterrupted, setIsCheckInterrupted } = props;

  const { t: translation } = useTranslation();

  useEffect(() => {
    if (isCheckInterrupted) {
      changeLoading(false);
    }
  }, [isCheckInterrupted]);

  const handleChange = ({ target }: React.ChangeEvent<HTMLInputElement>, field: 'username' | 'password' | 'changeIpUrl'): void => {
    const { proxy } = profileInfo;

    updateProfileInfo({
      proxy: {
        ...proxy,
        [field]: target.value?.trim(),
      },
    });
  };

  const prepareToProxyChecking = async (e: any) => {
    e.preventDefault();

    setIsCheckInterrupted(false);
    setProxyChecked(PROXY_CHECKED_INIT);

    const { proxy } = profileInfo;
    const options: any = {
      proxy,
      setProxyChecked,
    };

    const processCbs = () => {
      changeLoading(true);
    };

    const resCbs = () => {
      changeLoading(false);
    };

    checkProxy(options, processCbs, resCbs, translation);
  };

  const getCheckResult = () => {
    if (proxyChecked.status !== 'success' || isLoading || isCheckInterrupted) {
      return null;
    }

    return (
      <ProxyChecker
        isLoading={isLoading}
        proxyChecked={proxyChecked}
        geolocationShowOption={false}
        profileProxy={profileInfo.proxy}
        initialCheck={true}
        setCurrentDisplay={() => {}}
        setProxyChecked={() => {}}
      />
    );
  };

  const getFailCheckResult = () => {
    if (proxyChecked.status !== 'fail' || isCheckInterrupted) {
      return null;
    }

    return (
      <div style={{ color: 'red' }}>
        &nbsp;
        {proxyChecked.error}
      </div>
    );
  };

  const getLoadingSpinner = () => {
    if (!isLoading) {
      return null;
    }

    return (
      <LoaderDiv>
        <Icon type='loading' />
      </LoaderDiv>
    );
  };

  return (
    <>
      <ElementContainer>
        <Subtitle>
          {translation('base.login')}
        </Subtitle>
        <Input
          placeholder={translation('editProfile.proxy.loginInputPlaceholder') || ''}
          style={{ width: 278 }}
          value={profileInfo.proxy.username}
          onChange={(event): void => handleChange(event, 'username')}
        />
      </ElementContainer>
      <ElementContainer>
        <Subtitle>
          {translation('base.password')}
        </Subtitle>
        <Input
          placeholder={translation('editProfile.proxy.passwordInputPlaceholder') || ''}
          style={{ width: 278 }}
          value={profileInfo.proxy.password}
          onChange={(event): void => handleChange(event, 'password')}
        />
      </ElementContainer>
      <ElementContainer>
        <Subtitle>
          {translation('proxyForm.changeIPURL')}
        </Subtitle>
        <Input
          placeholder={translation('proxyForm.changeIP') || ''}
          style={{ width: 278 }}
          value={profileInfo.proxy.changeIpUrl}
          onChange={(event): void => handleChange(event, 'changeIpUrl')}
        />
      </ElementContainer>
      <CheckProxyButtonDiv>
        <SaveAndNextBtn
          href=''
          type='primary'
          disabled={isLoading}
          onClick={prepareToProxyChecking}
        >
          {translation('editProfile.proxy.checkProxyBtnText')}
        </SaveAndNextBtn>
        {getFailCheckResult()}
      </CheckProxyButtonDiv>
      {getLoadingSpinner()}
      {getCheckResult()}
    </>
  );
};

export default HttpSocksProxyForm;
