import { Icon, Tooltip } from 'antd';
import React, { FC } from 'react';
import { useTranslation } from 'react-i18next';

declare interface IClearButtonForm {
  updateProfileInfo: any;
  profileInfo: any;
}

const ClearButtonForm: FC<IClearButtonForm> = (props: IClearButtonForm) => {
  const { t: translation } = useTranslation();

  const { profileInfo, updateProfileInfo } = props;

  const clearProxy = (): void => {
    const { proxy } = profileInfo;
    updateProfileInfo({
      proxy: {
        ...proxy,
        port: '',
        host: '',
        username: '',
        password: '',
        changeIpUrl: '',
      },
    });
  };

  return (
    <Tooltip title={
      <span style={{ fontSize: 12, color: 'var(--000000-antd-tooltip-opacity)' }}>
        {translation('base.cleanForm')}
      </span>
    }>
      <div onClick={(): void => clearProxy()} style={{ paddingTop: 6, marginLeft: 2 }}>
        <Icon type='delete' style={{ fontSize: 18, opacity: 0.5, marginLeft: 8, cursor: 'pointer' }} />
      </div>
    </Tooltip>
  );
};

export default ClearButtonForm;
