import { Select } from 'antd';
import React, { FC } from 'react';

const { Option } = Select;

import { useTranslation } from 'react-i18next';

import { ElementContainer, Subtitle } from '../domElements';

declare interface ITorProxyForm {
  profileInfo: any;
  updateAutoProxyRegion: (val: string, field: string) => any;
}

const TorProxyForm: FC<ITorProxyForm> = (props: ITorProxyForm) => {
  const { t: translation } = useTranslation();

  return (
    <>
      <ElementContainer>
        <Subtitle>
          {translation('base.nodeExit')}
        </Subtitle>
        <Select
          value={props.profileInfo.proxy.torProxyRegion || 'us'}
          onChange={(val: string) => props.updateAutoProxyRegion(val, 'torProxyRegion')}
          style={{ width: 278 }}
        >
          <Option key="us">
            US (
            {translation('countries.us')}
            )
          </Option>
          <Option key="uk">
            UK (
            {translation('countries.uk')}
            )
          </Option>
          <Option key="de">
            DE (
            {translation('countries.de')}
            )
          </Option>
          <Option key="fr">
            FR (
            {translation('countries.fr')}
            )
          </Option>
          <Option key="eu">
            EU (
            {translation('countries.eu')}
            )
          </Option>
        </Select>
      </ElementContainer>
    </>
  );
};

export default TorProxyForm;
