
import { message } from 'antd';

import { isHostValid, isPortValid } from '../../common/proxy/validators/proxy.validators';
import { IProxyDataValidatorProps } from '../interfaces/proxy-data-valodator.interface';

export const isProxyDataValid = ({ host, port, translation }: IProxyDataValidatorProps): boolean => {
  const isValidHost = isHostValid(host);
  const isValidPort = isPortValid(port);
  if (!isValidHost) {
    message.error(translation('notifications.error.notValidHost'));

    return false;
  }

  if (!isValidPort) {
    message.error(translation('notifications.error.notValidPort'));

    return false;
  }

  return true;
};
