import { Select } from 'antd';
import React, { FC } from 'react';

const { Option } = Select;

import { useTranslation } from 'react-i18next';

import { ElementContainer, Subtitle } from '../domElements';

declare interface IGologinProxyForm {
  profileInfo: any;
  updateAutoProxyRegion: (val: string, field: string) => any;
}

const GologinProxyForm: FC<IGologinProxyForm> = (props: IGologinProxyForm) => {
  const { t: translation } = useTranslation();

  return (
    <>
      <ElementContainer>
        <Subtitle>
          {translation('base.country')}
        </Subtitle>
        <Select
          value={props.profileInfo.proxy.autoProxyRegion || 'us'}
          onChange={(val: string) => props.updateAutoProxyRegion(val, 'autoProxyRegion')}
          style={{ width: 278 }}
        >
          <Option key='us'>
            US (
            {translation('countries.us')}
            )
          </Option>
          <Option key='ca'>
            CA (
            {translation('countries.ca')}
            )
          </Option>
          <Option key='uk'>
            UK (
            {translation('countries.uk')}
            )
          </Option>
          <Option key='de'>
            DE (
            {translation('countries.de')}
            )
          </Option>
          <Option key='in'>
            IN (
            {translation('countries.in')}
            )
          </Option>
        </Select>
      </ElementContainer>
    </>
  );
};

export default GologinProxyForm;
