import { Input, Select } from 'antd';
import React from 'react';

const { Option } = Select;

import { useTranslation } from 'react-i18next';

import GologinProxyForm from './gologin-proxy-form';
import HttpSocksProxyForm from './http-socks-proxy-form';
import TorProxyForm from './tor-proxy-form';
import { PROXIES_DEFAULT } from '../../../proxy/constants';
import ClearButtonForm from '../components/clear-button-tooltip';
import { ElementContainer, Row, Subtitle } from '../domElements';

let ipcRenderer: Electron.IpcRenderer;
const isElectron = !!window.require;
if (isElectron) {
  ipcRenderer = window.require('electron').ipcRenderer;
}

declare interface IProxyForm {
  updateProfileInfo: any;
  profileInfo: any;
  setUpdatedField: (field: string) => any;
  isCheckInterrupted: boolean;
  setIsCheckInterrupted: React.Dispatch<React.SetStateAction<boolean>>;
}

const ProxyForm: React.FC<IProxyForm> = (props) => {
  const { updateProfileInfo, profileInfo, setUpdatedField, isCheckInterrupted, setIsCheckInterrupted } = props;

  const { t: translation } = useTranslation();
  const updateProxySelector = (proxyType: string) => {
    const { proxy } = profileInfo;
    const defaultProxy = PROXIES_DEFAULT.find(({ mode, country }) => mode === proxyType && country.toLowerCase() === proxy.torProxyRegion);
    updateProfileInfo({
      proxy: defaultProxy || { ...proxy, mode: proxyType },
    });

    setUpdatedField('proxy');
  };

  const updateAutoProxyRegion = (autoProxyRegion: string, mode: string) => {
    const { proxy } = profileInfo;

    updateProfileInfo({
      proxy: {
        ...proxy,
        [mode]: autoProxyRegion,
      },
    });
  };

  const handleChange = ({ target }: React.ChangeEvent<HTMLInputElement>, field: 'host' | 'port'): void => {
    const { proxy } = profileInfo;

    updateProfileInfo({
      proxy: {
        ...proxy,
        [field]: target.value.trim(),
      },
    });
  };

  const getProxyInputGroup = () => {
    if (!['http', 'socks4', 'socks5'].includes(profileInfo.proxy.mode)) {
      return null;
    }

    return (
      <>
        <Input
          placeholder='IP Address'
          style={{ width: 150 }}
          value={profileInfo.proxy.host}
          onChange={(event): void => handleChange(event, 'host')}
        />
        <Input
          style={{
            width: 10,
            padding: 3,
            borderLeft: 0,
            pointerEvents: 'none',
            backgroundColor: 'var(--FFFFFF)',
          }}
          placeholder=":"
          disabled={true}
        />
        <Input
          placeholder='port'
          style={{ width: 65, borderLeft: 0 }}
          value={profileInfo.proxy.port}
          onChange={(event): void => handleChange(event, 'port')}
        />
      </>
    );
  };

  const getClearButtonTooltip = () => {
    if (!['http', 'socks4', 'socks5'].includes(profileInfo.proxy.mode)) {
      return null;
    }

    return (
      <ClearButtonForm {...props} />
    );
  };

  const getProxySettingsContent = () => {
    const { mode } = profileInfo.proxy;

    if (mode === 'gologin') {
      return <GologinProxyForm {...props} updateAutoProxyRegion={updateAutoProxyRegion} />;
    }

    if (mode === 'tor') {
      return <TorProxyForm {...props} updateAutoProxyRegion={updateAutoProxyRegion} />;
    }

    return <HttpSocksProxyForm {...props} />;
  };

  return (
    <>
      <ElementContainer>
        <Subtitle>
          {translation('newProfile.proxy.proxyTypeHostAndPortLabel')}
        </Subtitle>
        <Row>
          <Input.Group compact={true} style={{ width: 500 }}>
            <Select
              value={profileInfo.proxy.mode}
              onChange={updateProxySelector}
              style={{ width: 278 }}
            >
              <Option key='http'>
                HTTP Proxy
              </Option>
              <Option key='socks4'>
                Socks 4 Proxy
              </Option>
              <Option key='socks5'>
                Socks 5 Proxy
              </Option>
              <Option key='gologin'>
                Free Proxy
              </Option>
              <Option key='tor'>
                Tor Network
              </Option>
            </Select>
            {getProxyInputGroup()}
          </Input.Group>
          {getClearButtonTooltip()}
        </Row>
      </ElementContainer>
      {getProxySettingsContent()}
    </>
  );
};

export default ProxyForm;
