import { Icon, message, Radio, Tooltip } from 'antd';
import React, { FC, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import ProxyForm from './customProxies/proxy-tab-custom-form';
import {
  ElementContainer,
  PasteIconContainer,
  RadioButton,
  TypeRow,
  BetaLabel,
} from './domElements';
import GeolocationProxyIndex from './geolocation/geolocation-proxy-index';
import { useQuery } from '../../../hooks';
import IconPasteHttp from '../../../ui/icons/IconPastHttp';
import { proxyFromClipboard } from '../../../utils/proxy-string';
import { sendActionAnalytics } from '../../common/api';

interface IProxyTab {
  updateProfileInfo: any;
  profileInfo: any;
  profileId?: string;
  showButton?: any;
  setUpdatedField: (field: string) => any;
  isSaveButtonPressed?: boolean;
  setIsGeolocationScreen?: (arg: boolean) => void;
  saveProfile?: () => void;
  isCreateProfile: boolean;
}

const CUSTOM_CONNECTION_TYPE = 'custom';
const proxyTypes = ['direct', 'geolocation', 'custom'] as const;
type ProxyType = typeof proxyTypes[number];

const ProxyTab: FC<IProxyTab> = (props: IProxyTab) => {
  const proxyQueryParam = useQuery().get('proxy');
  const proxyTypeInitial: ProxyType = proxyTypes.find(type => type === proxyQueryParam) || 'direct';

  const [connectionType, setConnectionType] = useState<ProxyType>(proxyTypeInitial);
  const [geolocationProxy, setGeolocationProxy] = useState<any>({});
  const [isCustomSelected, setIsCustomSelected] = useState<boolean>(false);
  const [isCheckInterrupted, setIsCheckInterrupted] = useState<boolean>(false);

  const { t: translation } = useTranslation();

  useEffect(() => {
    const { proxyEnabled, proxy } = props.profileInfo;
    const mode = proxy?.mode;
    let type: ProxyType = proxyTypeInitial;
    if (proxyEnabled) {
      type = mode === 'geolocation' ? 'geolocation' : CUSTOM_CONNECTION_TYPE;
    }

    if (type === 'custom') {
      setIsCustomSelected(true);
    }

    setConnectionType(type);

  }, [props.profileInfo.proxy.mode, props.profileId]);

  const updateProxyDefine = async () => {
    setIsCheckInterrupted(true);
    const pastedProxy = await proxyFromClipboard();

    if (!pastedProxy) {
      message.error(translation('notifications.error.invalidProxy'));

      return;
    }

    const { proxy } = props.profileInfo;
    props.updateProfileInfo({
      proxyEnabled: true,
      proxy: {
        ...proxy,
        ...pastedProxy,
      },
    });

    sendActionAnalytics('pasted proxy from buffer');

    setConnectionType(CUSTOM_CONNECTION_TYPE);
  };

  const showIconPasteProxy = () => {
    const { mode } = props.profileInfo.proxy;
    if (!['none', 'http', 'socks4', 'socks5'].includes(mode)) {
      return null;
    }

    return (
      <Tooltip
        title={
          (
            <span style={{ fontSize: 12, color: 'var(--000000-antd-tooltip-opacity)' }}>
              {translation('newProfile.proxy.paste.help')}
            </span>
          )
        }
      >
        <PasteIconContainer
          onClick={() => updateProxyDefine()}
        >
          <IconPasteHttp />
        </PasteIconContainer>
      </Tooltip>
    );
  };

  const getProxyModeTooltipInfo = () => {
    const proxyMode = props.profileInfo.proxy.mode;
    if (!['gologin', 'tor'].includes(proxyMode)) {
      return null;
    }

    let tooltipTitleTxt = translation('proxy.connection.gologin.help');
    if (proxyMode === 'tor') {
      tooltipTitleTxt = translation('proxy.connection.tor.help');
    }

    return (
      <>
        <Tooltip title={tooltipTitleTxt}>
          <Icon style={{ marginLeft: 10, cursor: 'pointer' }} type='info-circle' />
        </Tooltip>
      </>
    );
  };

  const getProxiesForm = () => {
    if (connectionType === CUSTOM_CONNECTION_TYPE) {
      return (
        <ProxyForm
          {...props}
          isCheckInterrupted={isCheckInterrupted}
          setIsCheckInterrupted={setIsCheckInterrupted}
        />
      );
    }

    if (connectionType === 'geolocation') {
      return (
        <GeolocationProxyIndex
          profileInfo={props.profileInfo}
          profileId={props.profileId}
          updateProfileInfo={props.updateProfileInfo}
          geolocationProxy={geolocationProxy}
          setGeolocationProxy={setGeolocationProxy}
          isCustomSelected={isCustomSelected}
          isSaveButtonPressed={props.isSaveButtonPressed}
          setIsGeolocationScreen={props.setIsGeolocationScreen}
          saveProfile={props.saveProfile}
          isCreateProfile={props.isCreateProfile}
        />
      );
    }

    return null;
  };

  const changeConnectionType = (type: 'direct'|'geolocation'|'custom') => {
    const proxyEnabled = ['custom', 'geolocation'].includes(type);
    let mode = type.valueOf();

    let proxy = {
      host: '',
      port: 80,
      torProxyRegion: 'us',
      autoProxyRegion: 'us',
      username: '',
      password: '',
    };

    if (type === 'direct') {
      mode = 'none';
    }

    if (type === 'custom') {
      if (proxy.mode === 'geolocation') {
        proxy = {
          host: '',
          port: 80,
          username: '',
          password: '',
        };
      }

      setGeolocationProxy({});
      mode = 'http';
    }

    props.updateProfileInfo({
      proxyEnabled,
      proxy: {
        ...proxy,
        mode,
      },
    });

    setConnectionType(type);
  };

  const getGeolocationProxyButton = () => (
    <RadioButton value='geolocation'>
      {translation('proxy.changeProxy.geolocation')}
      <BetaLabel>
        Beta
      </BetaLabel>
    </RadioButton>);

  return (
    <>
      <ElementContainer>
        <TypeRow>
          <Radio.Group
            value={connectionType}
            onChange={(e: any) => changeConnectionType(e.target.value)}
          >
            {getGeolocationProxyButton()}
            <RadioButton value='custom'>
              {translation('proxy.changeProxy.custom')}
            </RadioButton>
            <RadioButton value='direct'>
              {translation('proxy.changeProxy.direct')}
            </RadioButton>
          </Radio.Group>
          {showIconPasteProxy()}
          {getProxyModeTooltipInfo()}
        </TypeRow>
      </ElementContainer>
      <div style={{ marginBottom: 100 }}>
        {getProxiesForm()}
      </div>
    </>
  );
};

export default ProxyTab;
